@import 'mixin';
/*====== Event Block css start here ====== */
.eventList {
  @include pm-0;
  font-family: $font-family-roboto-slab;
  display: grid;
  grid-template-columns: auto auto auto;
  grid-gap: 6rem;
  &.eventList__slide {
    grid-gap: 0;
    .eventList__item {
      padding: 0 10px;
    }
    @media (min-width: 768px) {
      .eventList__item {
        padding: 0 20px;
      }
    }
    @media (min-width: 1200px) {
      .eventList__item {
        padding: 0 40px;
      }
    }
  }
  .eventList__item {
    @include pm-0;
    a {
      @include pm-0;
      text-decoration: none;
      color: inherit;
      .eventList__item--img {
        margin-bottom: 1.9rem;
      }
      .eventList__item--date {
        position: relative;
        display: flex;
        padding-bottom: 0.8rem;
        margin-bottom: 0.4rem;
        .eventDate {
          display: flex;
          margin-right: 15px;
          padding-right: 15px;
          position: relative;
          span {
            color: $darkblue;
            i {
              font-style: normal;
              display: block;
            }
          }
          .date {
            font-size: $font-size-base * 2;
            line-height: 0.8;
            margin-right: 10px;
          }
          .montYear {
            font-size: $font-size-base * 0.8;
            line-height: 1.1;
          }
          &:after {
            content: '';
            position: absolute;
            right: -4px;
            top: 50%;
            height: 2px;
            width: 8px;
            margin-top: -1px;
            background-color: $darkblue;
            @include transition-width03;
          }
          &:last-child {
            margin-right: 0;
            padding-right: 0;
            &:after {
              display: none;
            }
          }
        }
        &:after {
          content: '';
          position: absolute;
          @include lb-0;
          height: 2px;
          width: 40px;
          background-color: $lightgraylight-200;
          @include transition-width03;
        }
      }
      @media (max-width: 768px) {
        .eventList__item--date {
          padding-bottom: 0.8rem;
          margin-bottom: 0.4rem;
          .eventDate {
            .date {
              font-size: $font-size-base * 1.2;
              line-height: 0.8;
              margin-right: 10px;
            }
            .montYear {
              font-size: $font-size-base * 0.6;
              line-height: 1.1;
            }
          }
        }
      }
      .eventList__item--tag {
        color: $mediumblue;
        font-size: $font-size-base * 0.6875;
        padding: 0.3rem 0 0;
        text-transform: uppercase;
        font-family: $font-family-open-sans;
      }
      .eventList__item--entity {
        font-size: $font-size-base * 0.9;
        color: $darkblue;
        padding: 0.1rem 0;
        font-weight: $font-weight-light;
        font-family: $font-family-open-sans;
      }
      .eventList__item--title {
        font-size: $font-size-base * 1.3;
        color: $darkblue;
        padding: 0.3rem 0;
        line-height: 1.4;
      }
      @media (max-width: 768px) {
        .eventList__item--title {
          font-size: $font-size-base;
        }
      }
    }
  }
  .slick-prev {
    left: -30px;
  }
  .slick-next {
    right: -30px;
  }
}
/*====== Event Block css start here ====== */
